import {
	useBlockProps,
	InnerBlocks,
	InspectorControls,
} from "@wordpress/block-editor";
import { PanelBody, TextControl } from "@wordpress/components";
import { __ } from "@wordpress/i18n";
import "./editor.scss";
export default function Edit({ attributes, setAttributes }) {
	const { colClass } = attributes;

	const blockProps = useBlockProps({ className: colClass });

	return (
		<>
			<InspectorControls>
				<PanelBody title={__("Column Settings", "custom")} initialOpen={true}>
					<TextControl
						label={__("Column Class", "custom")}
						value={colClass}
						onChange={(val) => setAttributes({ colClass: val })}
						help={__("Example: col, col-md-auto, col-lg-2", "custom")}
					/>
				</PanelBody>
			</InspectorControls>
			<div {...blockProps}>
				<InnerBlocks />
			</div>
		</>
	);
}
