import { useBlockProps, InnerBlocks } from "@wordpress/block-editor";

export default function save({ attributes }) {
	const { colClass } = attributes;
	const blockProps = useBlockProps.save({ className: colClass });

	return (
		<div {...blockProps}>
			<InnerBlocks.Content />
		</div>
	);
}
